#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.App {
  height: 65vh;
  width: 80vw;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
